<template lang="pug">
  vx-card(v-if="article.html")
    div(v-html="article.html")
</template>

<script>
export default {
  name: 'article-one',
  data: () => ({
    article_id: '',
    article: {}
  }),
  watch: {
    '$route.params.article_id': function (article_id) {
      this.article_id = article_id
      this.article = this.$store.getters.articleById(this.article_id)
    }
  },
  async created() {
    this.article_id = this.$route.params.article_id
    this.article = this.$store.getters.articleById(this.article_id)
    window.addEventListener('click', event => {
      let { target } = event
      while (target && target.tagName !== 'A') target = target.parentNode
      if (target && target.matches("a:not([href*='://'])") && target.href) {
        const { altKey, ctrlKey, metaKey, shiftKey, button, defaultPrevented } = event
        if (metaKey || altKey || ctrlKey || shiftKey) return
        if (defaultPrevented) return
        if (button !== undefined && button !== 0) return
        if (target && target.getAttribute) {
          const linkTarget = target.getAttribute('target')
          if (/\b_blank\b/i.test(linkTarget)) return
        }
        const url = new URL(target.href)
        const to = url.pathname
        if (window.location.pathname !== to && event.preventDefault) {
          event.preventDefault()
          const article_id = to.split('/').slice(-1)[0]
          this.$router.push({ name: 'article-one', params: { article_id }})
        }
      }
    })
  }
}
</script>

<style>
img {
  max-width: 100%;
}
</style>
